ata-aos":"fade-down"}},[_vm._v("Por otro lado, la necesidad de contar con información es de gran importancia para las organizaciones, al mismo tiempo toda empresa necesita tener acceso a la mayor cantidad de información posible. No importa el área funcional de la empresa, cada uno de los elementos que la integran necesitan en mayor o menor grado.")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-down"}},[_vm._v("Para el nivel directivo en una empresa, la información es utilizada para diseñar estrategias y trazar el camino que se debe seguir, por tal motivo es de vital importancia que esta información sea oportuna y especifica. En el área intermedia de la información se utiliza para diseñar estrategias específicas que permitan el logro de los objetivos que se establecieron en el nivel directivo. Por otro lado, en el nivel operativo, la información que se maneja es detallada en los procesos, productos, actividades que los trabajadores necesitan para llevar a cabo las tareas de sus respectivas áreas.")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-down"}},[_vm._v("A continuación, se revisará cómo es el manejo de la información en una organización en los diferentes niveles.")]),_vm._m(5),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-down"}},[_vm._v("La información está inmersa en cualquier actividad de la vida cotidiana y por tanto es de gran importancia para entender un asunto a realizar en cualquier ámbito, ya sea en el trabajo o en cualquier otro campo. La información es necesaria e importante para cualquier proceso que realice una organización, pues permite que se evite cometer errores, existen cinco (5) tipos de información, los cuales son:")]),_c('TabsC',{staticClass:"color-acento-contenido mb-5",attrs:{"data-aos":"fade-down"}},[_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Información confidencial"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-5 col-lg-4 col-12 mb-4 mb-md-0"},[_c('figure',[_c('img',{attrs:{"src":require("